import { LAppDelegate } from './lappdelegate';
import * as LAppDefine from './lappdefine';

export function loadv2(canvasId, options, token) {
  if (token && token.sign) {
    fetch(options.sdkUrl, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/javascript'
      },
      cache: 'default'
    }).then(response => response.text()).then(scriptText => {
      if (!LAppDelegate.getInstance().initialize(canvasId, options.modelApi, options, token)) {
        return;
      }
      LAppDelegate.getInstance().run();
    })
  } else {
    console.warn("需要与插件官网链接后才可以使用, 请先登录, 或访问https://www.live2dweb.com/ 了解更多");
  }
}
export function screenshotV2(captureName, captureFrame) {
  LAppDelegate.getInstance()._captureFrame = captureFrame;
  LAppDelegate.getInstance()._captureName = captureName;
}
/**
 * 終了時の処理
 */
window.onbeforeunload = () => LAppDelegate.releaseInstance();

/**
 * Process when changing screen size.
 */
window.onresize = () => {
  if (LAppDefine.CanvasSize === 'auto') {
    LAppDelegate.getInstance().onResize();
  }
};
